@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswald-regular.woff2") format("woff2"), url("../fonts/oswald/oswald-regular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
  }
}
.page {
  height: 100%;
}

.page__body {
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  margin: 0;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 13px 20px 15px 20px;
}

.main-header__logo-image--full {
  display: none;
}

.main-header__logo {
  display: flex;
  align-items: center;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.6;
}
.main-header__logo:focus {
  outline: 1px solid #0085f2;
}

.main-header__logo-image--simple {
  margin-right: 56px;
}

@media (min-width: 768px) {
  .main-header {
    padding: 25px 30px 0 30px;
  }

  .main-header__logo-image--simple {
    display: none;
  }

  .main-header__logo-image--full {
    display: block;
    margin-right: 13px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding: 55px 110px 0 110px;
  }
}
.main-container__header {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
  color: #000000;
  margin-top: 27px;
  margin-bottom: 37px;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 768px) {
  .main-container__header {
    font-size: 60px;
    line-height: 60px;
    margin-top: 65px;
    margin-bottom: 84px;
    padding-left: 30px;
    padding-right: 30px;
  }
  .main-container__header--form {
    margin-bottom: 54px;
    padding-left: 70px;
    padding-right: 70px;
  }
}
@media (min-width: 1440px) {
  .main-container__header {
    margin-top: 74px;
    margin-bottom: 84px;
    padding-left: 110px;
    padding-right: 110px;
  }
  .main-container__header--form {
    margin-bottom: 55px;
  }
}
.main-nav {
  color: #000000;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

.main-nav__toggle {
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  background-color: transparent;
  background-image: url("../img/button-toggle-burger.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.main-nav__list {
  display: none;
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-nav__list--opened {
  position: absolute;
  width: 100%;
  left: 0;
  top: 66px;
  background-color: #ffffff;
  display: block;
}

.main-nav__link {
  display: block;
  padding-top: 22px;
  padding-bottom: 24px;
  text-decoration: none;
  color: #000000;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link:focus {
  outline: 1px solid #0085f2;
}
.main-nav__link--white:hover {
  opacity: 0.6;
}
.main-nav__link--white:active {
  opacity: 0.3;
}
.main-nav__link--white:focus {
  outline: 1px solid #ffffff;
}

.main-nav__item {
  text-align: center;
  border-top: 1px solid #e6e6e6;
}
.main-nav__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}

@media (min-width: 768px) {
  .main-nav {
    font-size: 18px;
    line-height: 24px;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__list {
    display: flex;
    gap: 33px;
  }
  .main-nav__list--opened {
    position: initial;
    display: flex;
    background-color: transparent;
  }

  .main-nav__item {
    border-top: none;
  }
  .main-nav__item:last-child {
    border-bottom: none;
  }

  .main-nav__link {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .main-nav__link--active {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    font-size: 20px;
    line-height: 20px;
  }

  .main-nav__link--white {
    color: #ffffff;
    border-color: #ffffff;
  }
}
.title {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
}

@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}
.promo {
  text-align: center;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85) 303px, transparent 303px), url("../img/promo-cat.jpg"), linear-gradient(#4c4b50 303px, transparent 303px);
  background-repeat: no-repeat;
  background-position: top center;
}

.promo__wrapper {
  padding: 27px 20px 20px 20px;
}

.promo__header {
  color: #ffffff;
  margin: 0;
  margin-bottom: 25px;
}

.promo__text {
  color: #ffffff;
  margin: 0;
  font-family: "Oswald", "Tahoma", sans-serif;
  line-height: 14px;
  text-transform: uppercase;
  margin-bottom: 29px;
}

.promo__link {
  width: 100%;
}

@media (min-width: 768px) {
  .promo {
    text-align: left;
    background-image: none;
  }

  .promo__wrapper {
    position: relative;
    padding: 65px 30px 0 30px;
  }

  .promo__header {
    color: #000000;
    text-align: left;
    margin-left: 90px;
    margin-right: 180px;
    margin-bottom: 40px;
  }

  .promo__text {
    color: #000000;
    line-height: 20px;
    font-size: 20px;
    text-align: left;
    margin-left: 90px;
    margin-bottom: 21px;
  }

  .promo__image {
    width: 709px;
    height: 609px;
  }

  .promo__link {
    position: absolute;
    width: auto;
    left: 120px;
    bottom: 526px;
  }
}
@media (min-width: 1440px) {
  .promo__background {
    height: 695px;
    width: 100%;
    position: absolute;
    top: 0;
    z-index: -1;
    background-image: linear-gradient(90deg, transparent 50%, rgba(104, 183, 56, 0.85) 50%), url("../img/promo-cat-desktop.jpg"), linear-gradient(90deg, transparent 50%, #4c4b50 50%);
    background-repeat: no-repeat;
    background-position: right top;
  }

  .promo__wrapper {
    min-height: 590px;
    padding-top: 110px;
    padding-left: 0;
  }

  .promo__image {
    position: absolute;
    width: 552px;
    height: 499px;
    bottom: 52px;
    right: 331px;
  }

  .promo__link {
    position: initial;
    margin-left: 190px;
  }

  .promo__header {
    width: 420px;
    margin-left: 190px;
    margin-bottom: 40px;
  }

  .promo__text {
    margin-left: 190px;
    margin-bottom: 52px;
  }
}
.button {
  display: inline-block;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  border: none;
  border-radius: 0;
  text-decoration: none;
  padding: 14px 26px;
  color: #ffffff;
  background-color: #68b738;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button:focus {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
  outline: 1px solid #0085f2;
}
.button--grey {
  background-color: #f2f2f2;
  color: #444444;
}
.button--grey:hover {
  background-color: #ebebeb;
}
.button--grey:active {
  background-color: #ebebeb;
  color: #444444;
}
.button--grey:focus {
  outline: 1px solid #0085f2;
  color: #444444;
  background-color: #ebebeb;
}

@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 26px;
  }
}
.goal {
  padding-left: 20px;
  padding-right: 20px;
}

.goal__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.goal__header {
  color: #000000;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
  margin: 0;
  padding-left: 70px;
  margin-bottom: 29px;
}

.goal__text {
  margin: 0;
  margin-bottom: 23px;
}

.goal__link {
  display: block;
  text-decoration: none;
  color: #000000;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  border-top: 1px solid #d9d9d9;
  padding-top: 15px;
}
.goal__link::after {
  content: "";
  display: inline-block;
  width: 24px;
  height: 12px;
  background-image: url("../img/goal-arrow.svg");
  background-repeat: no-repeat;
  background-position: center right;
  margin-left: 15px;
}
.goal__link:hover::after {
  width: 32px;
}
.goal__link:active {
  opacity: 0.3;
}
.goal__link:active::after {
  width: 32px;
}
.goal__link:focus {
  outline: 1px solid #0085f2;
}

.goal__item {
  background-color: #f2f2f2;
  background-repeat: no-repeat;
  margin-bottom: 20px;
  padding: 30px 20px 18px 20px;
}
.goal__item--slim {
  background-image: url("../img/cat-slim.svg");
  background-position: 20px 21px;
  background-size: 50px 53px;
}
.goal__item--pro {
  background-image: url("../img/cat-pro.svg");
  background-position: 13px 24px;
  background-size: 67px 50px;
}
.goal__item:last-child {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .goal {
    padding-left: 30px;
    padding-right: 30px;
  }

  .goal__header {
    font-size: 36px;
    line-height: 36px;
    padding-left: 0;
    margin-bottom: 24px;
  }

  .goal__text {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 52px;
  }

  .goal__item {
    min-height: 372px;
    padding: 77px 316px 57px 53px;
    margin-bottom: 30px;
  }
  .goal__item--slim {
    background-size: 200px 211px;
    background-position: calc(100% - 63px) center;
  }
  .goal__item--pro {
    background-size: 268px 200px;
    background-position: calc(100% - 29px) center;
  }

  .goal__link {
    display: flex;
    border-top: 0;
    font-size: 20px;
    line-height: 20px;
    align-items: center;
  }
}
@media (min-width: 1440px) {
  .goal {
    padding-top: 82px;
    padding-left: 110px;
    padding-right: 110px;
  }

  .goal__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }

  .goal__item {
    padding: 77px 52px 55px 52px;
    margin-bottom: 0;
  }
  .goal__item--slim {
    background-size: 110px 106px;
    background-position: 52px 41px;
  }
  .goal__item--pro {
    background-size: 134px 100px;
    background-position: 33px 47px;
  }

  .goal__header {
    padding-left: 162px;
    margin-bottom: 73px;
  }

  .goal__text {
    margin-bottom: 27px;
  }

  .goal__link {
    padding-top: 0;
  }
}
.explanation {
  padding: 20px 20px 49px 20px;
}

.explanation__header {
  color: #000000;
  margin: 0;
  margin-bottom: 40px;
}

.explanation__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.explanation__item {
  margin-bottom: 26px;
  background-repeat: no-repeat;
  background-position: left center;
  padding-left: 80px;
}
.explanation__item--leaf {
  background-image: url("../img/explanation-leaf.svg");
}
.explanation__item--water {
  background-image: url("../img/explanation-water.svg");
}
.explanation__item--eat {
  background-image: url("../img/explanation-eat.svg");
}
.explanation__item--time {
  background-image: url("../img/explanation-time.svg");
}
.explanation__item:last-child {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .explanation {
    padding: 45px 30px 47px 30px;
    border-bottom: 1px solid #e6e6e6;
  }

  .explanation__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 65px;
    row-gap: 77px;
    counter-reset: count 0;
  }

  .explanation__item {
    line-height: 24px;
    font-size: 16px;
    padding-left: 0;
    padding-top: 101px;
    padding-right: 70px;
    background-position: left top;
    background-size: 80px 80px;
    position: relative;
    min-height: 240px;
    margin-bottom: 0;
    counter-increment: count 1;
  }
  .explanation__item::before {
    content: counter(count);
    color: #f2f2f2;
    font-family: "Oswald", "Tahoma", sans-serif;
    font-size: 280px;
    font-weight: 400;
    line-height: 280px;
    position: absolute;
    top: -40px;
    left: 115px;
    z-index: -1;
  }

  .explanation__header {
    margin-bottom: 72px;
  }
}
@media (min-width: 1440px) {
  .explanation {
    border-bottom: none;
    padding: 71px 110px 69px 110px;
  }

  .explanation__list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .explanation__item {
    padding-right: 0;
  }
}
.example {
  background-color: #eaeaea;
}

.example__wrapper {
  padding: 25px 20px 40px 20px;
}

.example__header {
  margin: 0;
  color: #000000;
  margin-bottom: 40px;
}

.example__text {
  margin: 0;
  margin-bottom: 20px;
}

.example__list {
  display: grid;
  margin: 0;
  margin-bottom: 20px;
  grid-template-rows: 56px min-content;
  grid-auto-columns: 124px;
  column-gap: 32px;
  text-align: center;
}

.example__item-name {
  grid-row: 2/3;
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
  transform: translateY(-8px);
}

.example__item-name-text {
  background-color: #eaeaea;
  display: block;
  width: 85px;
  padding: 0 10px;
  margin-left: auto;
  margin-right: auto;
}

.example__item-value {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-row: 1/2;
  margin: 0;
  color: #000000;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 24px;
  line-height: 24px;
  white-space: nowrap;
  border-radius: 3px;
  border: 1px solid #cdcdcd;
}

.example__expenses {
  display: flex;
  gap: 5px;
  justify-content: center;
  color: #000000;
  text-align: center;
  font-family: "Oswald", "Tahoma", sans-serif;
  line-height: 20px;
  text-transform: uppercase;
}

.example__photo {
  position: relative;
  height: 256px;
}

.example__photo-before {
  position: absolute;
  height: 100%;
  width: 50%;
  background-image: url("../img/fat-cat.png");
  background-size: 280px 100%;
  background-position: left center;
  background-repeat: no-repeat;
  background-color: #eaeaea;
  z-index: 2;
}

.example__photo-after {
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: url("../img/slim-cat.png");
  background-size: 280px 100%;
  background-position: left center;
  background-repeat: no-repeat;
  background-color: #eaeaea;
  z-index: 1;
}

.example__photo-toggle {
  position: absolute;
  z-index: 10;
  padding: 0;
  width: 4px;
  height: 100%;
  background-color: #666666;
  border: none;
  border-radius: 2px;
  top: 0;
  transform: translateX(-50%);
  cursor: col-resize;
}
.example__photo-toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ffffff;
  border: 2px solid #666666;
  background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Vector'%3E%3Cpath d='M16 11L8 20L16 29V11Z' fill='%23666666'/%3E%3Cpath d='M32 20L24 11V29L32 20Z' fill='%23666666'/%3E%3C/g%3E%3C/svg%3E");
  background-position: center;
  background-repeat: no-repeat;
}
.example__photo-toggle:hover {
  background-color: #68b738;
}
.example__photo-toggle:hover::before {
  border-color: #68b738;
  background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Vector'%3E%3Cpath d='M16 11L8 20L16 29V11Z' fill='%2368b738'/%3E%3Cpath d='M32 20L24 11V29L32 20Z' fill='%2368b738'/%3E%3C/g%3E%3C/svg%3E");
}
.example__photo-toggle:active {
  background-color: #68b738;
  opacity: 0.6;
}
.example__photo-toggle:active::before {
  border-color: #68b738;
  background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Vector'%3E%3Cpath d='M16 11L8 20L16 29V11Z' fill='%2368b738'/%3E%3Cpath d='M32 20L24 11V29L32 20Z' fill='%2368b738'/%3E%3C/g%3E%3C/svg%3E");
}
.example__photo-toggle:focus::before {
  outline: 1px solid #0085f2;
}

@media (min-width: 768px) {
  .example {
    background-color: transparent;
    background-image: linear-gradient(0deg, #eaeaea 462px, transparent 462px);
  }

  .example__wrapper {
    padding: 45px 30px 101px 30px;
    display: grid;
    grid-template-columns: 2fr 1fr;
  }

  .example__expenses {
