  .example__list {
    grid-auto-columns: 162px;
    column-gap: 43px;
    grid-template-rows: 72px min-content;
    margin-bottom: 0;
  }

  .example__item-name {
    font-size: 14px;
    line-height: 14px;
  }

  .example__item-value {
    font-size: 30px;
