  }
}
@media (min-width: 1440px) {
  .example {
    background-image: linear-gradient(0deg, #f2f2f2 462px, transparent 462px);
  }

  .example__wrapper {
    padding: 0 110px 75px 110px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 206px min-content 132px max-content;
    column-gap: 100px;
  }

  .example__header {
    margin-bottom: 0;
    grid-column: 1/2;
  }

  .example__text {
    grid-column: 1/2;
    margin-bottom: 69px;
    padding-right: 95px;
  }

  .example__list {
    grid-column: 1/2;
  }

  .example__expenses {
    grid-column: 1/2;
    gap: 63px;
    flex-direction: row;
    justify-content: initial;
  }

  .example__photo {
    grid-row: 1/-1;
    grid-column: 2/3;
    padding-bottom: 10px;
    margin-top: 0;
  }

  .example__photo-before {
    background-image: url("../img/fat-cat.png"), linear-gradient(0deg, #f2f2f2 377px, #ffffff 377px);
  }

  .example__item-name-text {
    background-color: #f2f2f2;
  }
