.input--textarea {
  width: 100%;
  height: 150px;
  border: 1px solid #e7e7e7;
  padding: 14px 14px 14px 15px;
}
.input:hover {
  border-color: #cdcdcd;
}
.input:focus {
  border-color: #444444;
  outline: 1px solid #444444;
}
.input:invalid {
  border-color: #ff8282;
  outline: 1px solid #ff8282;
}

@media (min-width: 768px) {
  .input {
    font-size: 20px;
    line-height: 20px;
    padding: 11px 20px;
  }
  .input--textarea {
    line-height: 30px;
    margin-top: 15px;
    margin-left: 70px;
    margin-right: 70px;
    width: calc(100% - 140px);
  }
}
@media (min-width: 1440px) {
  .input {
    padding: 10px 20px;
  }
  .input.input--textarea {
    margin-top: 2px;
    margin-left: 40px;
    margin-right: 40px;
    width: calc(100% - 80px);
  }
}
.control {
  display: flex;
  gap: 15px;
}

.control__radio {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #cdcdcd;
}

.control__input:checked ~ .control__radio::after {
  display: block;
  content: "";
  border-radius: 50%;
  background: #68b738;
  width: 14px;
  height: 14px;
}

.control__input:hover ~ .control__radio {
  border-color: #bababa;
}

.control__input:disabled ~ .control__radio {
  border-color: #bababa;
  opacity: 0.3;
}

.control__input:disabled ~ .control__label {
  opacity: 0.3;
}

.control__input:focus ~ .control__radio {
  outline: 1px solid #0085f2;
}

.control__checkbox {
  width: 26px;
  height: 26px;
  border: 1px solid #cdcdcd;
}

.control__input:checked ~ .control__checkbox::after {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-image: url("../img/checkbox-tick.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.control__input:hover ~ .control__checkbox {
  border-color: #bababa;
}

.control__input:focus ~ .control__checkbox {
  outline: 1px solid #0085f2;
}

.control__input:disabled ~ .control__checkbo {
  border-color: #bababa;
  opacity: 0.3;
}

@media (min-width: 768px) {
  .control {
    gap: 20px;
    padding-right: 0;
    padding-left: 0;
  }
}
@media (min-width: 1440px) {
  .control__radio {
    width: 40px;
    height: 40px;
  }

  .control__input:checked ~ .control__radio::after {
    width: 18px;
    height: 18px;
  }
}
.social__link:focus path {
  fill: #68b738;
}

.social__link:hover path {
  fill: #68b738;
}

.social__link:active path {
  fill: #68b738;
  opacity: 0.3;
}